.grid {
  display: grid;
  grid-column-gap: 0.8rem;
  -webkit-column-gap: 0.8rem;
          column-gap: 0.8rem;
  margin-left: 0.2rem;
  grid-template-columns: repeat(2,1fr);
  text-align: center;

}
.grid img {
  width: 180px;
  height: 150px;
}
.grid img:hover {
  -webkit-transform: scale(1.2);
