/* Bubbles */

.bubbles{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.bubble{
  position: absolute;
  bottom: 0;
  background: radial-gradient( #fff,#666666);
  border-radius: 50%;
  opacity: 0.8;
  -webkit-animation: flying 10s infinite ease-in;
          animation: flying 10s infinite ease-in;
}
.bubble:nth-child(1){
  width: 40px;
  height: 40px;
  left: 10%;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}

.bubble:nth-child(2){
  width: 20px;
  height: 20px;
  left: 20%;
  -webkit-animation-duration: 5s;
