@-webkit-keyframes flying{
  0%{
    bottom: 0px;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  
  100%{
    bottom: 100%;
    -webkit-transform: translateY(-600px);
            transform: translateY(-600px);
  }
}
@keyframes flying{
  0%{
    bottom: 0px;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
  }
  
  100%{
    bottom: 100%;
    -webkit-transform: translateY(-600px);
            transform: translateY(-600px);
  }
